.container-header-deportes{
    width: 100%;
    position: sticky;
    top: 0px;
    z-index: 1000; 
    .header-deportes{
        background-color: var(--live-color);
        border-bottom: 2px solid var(--deportes-color);
        position: sticky;
        top: 0;
        z-index: 3; 
        .header-deportes-wrapper{
            max-width: var(--sm-container);
            margin: auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .menu{
                display: flex;
                align-items: center;
                justify-content: space-between;
                .logo-noticias{
                    
                    color: white;
                    width:70px;
                    height: auto;
                    
                    a{
                        color: rgb(255, 0, 0);
                    }
                }
                .logo-deportes{
                    background-color: var(--deportes-color);
                    padding: 10px;
                    img{
                        color: white;
                        width:70px;
                        height: auto;
                    }
                }
                .logo-homepage{
                    background-color: #00000000;
                }
                nav{
                    padding-left: 20px;
                    display: flex;
                    justify-content: space-between;
                    gap: 15px;
                    color: rgb(0, 0, 0);
                    li{
                        font-weight: 200;
                        font-size: 18px; 
                        font-family: 'Sofia Sans', sans-serif;
                        text-transform: uppercase;
                    }
                    li:hover{
                        text-decoration: underline;
                        text-decoration-color: white;
                        text-decoration-thickness: 1px;
                        text-decoration-skip-ink: none;
                        text-underline-offset: 5px;   
                    }
                }
            }
            .right-header{
                display: flex;
                align-items: center;
                justify-content: space-between; 
                .profile-avatar{
                    display: flex;
                    gap: 5px;
                    align-items: center;
                    span{
                        font-size: 12px;
                        font-weight: 200;
                        img{
                            width: 40px;
                            height: 40px;
                        }
                        a{
                            font-size: 18px;
                        }
                    }
                }
                .user{
                    color: white;
                    border-right: 1px solid white;
                    padding: 0px 10px;
                    a{
                        color: white;
                        text-transform: uppercase;
                    }
                }
                .social-media-header{ 
                    a{
                        padding-left: 10px;
                        color: white;
                        font-size: 15px;
                    }
                    a:hover{
                        color: var(--secondary-color);
                    }
                }
                .al-aire{
                    display: flex;
                    justify-content: center;
                    width: 100px;
                    background-color: #ff000000;
                    .vivo{
                        width: 80px;
                        padding-left: 15px;
                        position: relative;
                        text-align: center;
                        span{
                            content: '';
                            position: absolute;
                            top: 35%;
                            left: 0px;
                            width: 8px;
                            height: 8px;
                            border-radius: 100%;
                            background-color: rgb(255, 255, 255);
                            border: 1px solid #ff0000;
                            -webkit-animation: live 1.4s infinite ease-in-out;
                            animation: live 1.4s infinite ease-in-out;
                            -webkit-animation-fill-mode: both;
                            animation-fill-mode: both;
                        }
                        
                        span{
                            animation: blink-animation 1s steps(5, start) infinite;
                            -webkit-animation: blink-animation 1s steps(5, start) infinite;
                          }
                          @keyframes blink-animation {
                            0%, 80%, 100% {  
                              transform: scale(.1);
                              -webkit-transform: scale(.1) ;
                            }
                            40%{
                                transform: scale(1);
                                -webkit-transform: scale(1) ;
                              }
                          }
                        span:nth-child(2){
                            -webkit-animation-delay: .16s;
                            animation-delay: .16s;
                            background-color: #ff0000;
                        }
                    }
                    .vivo-header{
                        padding: 5px 0px;
                        border-radius: 5px 5px 5px 5px;
                        background-color: #00000000;
                        color: #ffffff;  
                        a{
                            color: rgb(255, 255, 255);
                            font-size: 15px;
                            font-weight: 400;
                            font-family: 'Sofia Sans', sans-serif;
                        }
                    }
                    .vivo-header-deportes{
                        border-radius: 5px 5px 5px 5px;
                        padding: 2px 0px;
                        background-color: var(--deportes-color)!important; 
                        a{
                            color: rgb(255, 255, 255);
                            font-size: 15px;
                            font-weight: 400;
                            font-family: 'Sofia Sans', sans-serif;
                        } 
                    }
                }
            }
        }
    }
}



.nav-deportes li {
    color: #ffffff !important;
}



.logo-deportes img{
    color: white;
    width:70px;
    height: auto;
}



.container-header-deportes .noticias-heading-title:before{
    background-color: var(--deportes-color)!important;
}

.nav-deportes li a{
    color: #ffffff !important;
}
.container-header-deportes .main-top{
    margin-bottom: 50px;
    background-color: white;
    padding: 15px;
}
.container-header-deportes .lastest-deportes{
    background-color: white;
    padding: 15px;
    position: sticky;
    top: 100px;
}
.social-media a{   
    font-size: 15px;
}
.container-header-deportes .most-popular{
    background-color: white;
    padding: 15px;
    height: auto !important;
    position: sticky;
    top: 100px;
}
.container-header-deportes .main-middle{
    background-color: white;
    padding: 15px;
}
.container-header-deportes .homepage-noticias{
    background-color: #26292a00;
}
.icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25px;
    height: 25px;
    /* border: 1px solid  #767676; */
    border-radius: 100%;
    font-size: 20px;
    gap: 15px;
    background-color: #efefef;
    cursor: pointer;
}


