.list-cards{
    display: flex;
    margin-bottom: 20px;
}
.list-cards .card{
    flex-basis: 37%;
    border-radius: 5px;
    overflow: hidden;
    height: fit-content;
}
.list-cards .content{
    padding-left: 10px;
    font-size: 10px;
    flex-basis: 63%;
}
.main-post .post-categories a{
    font-size: 15px;
}
.main-post h2{
    font-weight: 400;
    line-height: 30px;
}
.main-post .content{
    padding-top: 10px;
}


.list-post{
    margin-bottom: 0px;
    padding: 0px;
}

.main-out{
    padding: 15px 15px;
    grid-column-start: 1;
    grid-column-end: 4;
}
.social-media a{   
    font-size: 15px;
}
.main-middle{
    width: 100%;
    margin: auto;
    margin-bottom: 60px;  

